@media (orientation: landscape) {

  * {
    padding:0;
    margin:0;
    font-size: 1.7vw;
  }

  #page_container {
    display: flex;
    flex-direction: row;

    justify-content: center;
    align-items: center;
  }

  #page_container h1 {
    margin-top: 1vh;
    color: #ffcd00;
    -webkit-text-stroke:0.05vw black;

    font-family: Impact, Haettenschweiler, 'Arial Narrow Bold', sans-serif;
    font-size: 2vw;
  }

  #home_container {
    display: flex;
    flex-direction: column;

    justify-content: center;
    align-items: center;

    margin-top: 20vh;

    background-color: rgb(184, 208, 220);

    border-style: solid;
    border-color: rgb(31, 134, 186);
    border-radius: 1.5vh;

    width: 40vw;
  }

  #home_logo {
    width: 18vw;
    margin-top: 1vh;
  }

  #search_bar {
    height: 4vh;
    width: 50%;
    border: 0px;

    font-size: 1vw;

    margin-top: 3vh;

    padding-left: 0.5vw;

    border-radius: 1.5vh;

    border-style: solid;
    border-color: black;
    border-width: 0.2vw;
  }

  #search_icon {
    color: black;
    scale: 80%;
  }

  #search_button {
    height: 50%;
    width: 10%;

    margin-top: 1vh;
    margin-bottom: 1vh;

    border: 0;
    border-radius: 1.5vh;

    border-style: solid;
    border-color: black;
    border-width: 0.2vw;
  }

  #search_button:hover {
    scale: 105%;
    transition-duration: 0.3s;
    cursor: pointer;
  }

  #search_button:active {
    scale: 95%;
    transition-duration: 0s;
  }

}

